<template>
  <div id="container" class="container">
    <img class="banner" :src="bannerImg" alt />
    <div class="main-content">
      <h2 class="container-title">新闻资讯</h2>
      <el-tabs class="container-tabs" v-model="activeName" @tab-click="onTabClick" tab-position="left">
        <el-tab-pane class="tabs-pane" name="news" label="新闻">
          <div class="tabs-content">
            <div class="item" v-for="(item, index) in news" :key="index">
              <el-image :src="item.pic" alt fit="cover" />
              <div class="info">
                <div class="title">{{item.title}}</div>
                <div class="time">{{item.date}}</div>
                <p class="note">{{item.note}}</p>
                <a class="btn" :href="'/news/newsDetail/' + item.id" target="_blank">查看详情</a>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane class="tabs-pane" name="infos" label="资讯">
          <div class="tabs-content">
            <div class="item" v-for="(item, index) in infos" :key="index">
              <el-image :src="item.pic" alt fit="cover" />
              <div class="info">
                <div class="title">{{item.title}}</div>
                <div class="time">{{item.date}}</div>
                <p class="note">{{item.note}}</p>
                <a class="btn" :href="'/news/newsDetail/' + item.id" target="_blank">查看详情</a>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
      <div class="pagi-wrapper">
        <el-pagination
          class="pagi"
          background
          @current-change="onPageChange"
          :page-size="5"
          :total="activeName === 'news' ? newsCount : infosCount"
        />
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { getInfos, getNews } from "@/utils/service";
import NewsCard from "./Components/NewsCard";
import router from "../../router";

export default {
  name: "container",
  components: {
    "news-card": NewsCard
  },
  data() {
    return {
      news: undefined,
      infos: undefined,
      newsCount: undefined,
      infosCount: undefined,
      activeName: "news",
      pageSize: 5,
      bannerImg: require("@/assets/banner1.jpeg"),
      testImg: "https://s2.ax1x.com/2019/12/15/QWXQW8.png"
    };
  },
  filters: {},
  mounted() {
    this.getNewsData();
    this.getInfosData();
  },
  methods: {
    getNewsData: function(pageIndex = 1) {
      getNews(pageIndex, this.pageSize).then(v => {
        if (!v) {
          return;
        }

        const { ok, data, count } = v;
        if (ok) {
          const newData =
            v.data &&
            v.data.map(v => {
              v.note = v.content.substring(0, 100) + "...";
              return v;
            });
          this.news = newData;
          this.newsCount = v.count;
        } else {
          $message("新闻加载失败");
        }
      });
    },
    getInfosData: function(pageIndex = 1) {
      getInfos(pageIndex, this.pageSize).then(v => {
        if (!v) {
          return;
        }

        const { ok, data, count } = v;
        if (ok) {
          const newData =
            v.data &&
            v.data.map(v => {
              v.note = v.content.substring(0, 100) + "...";
              return v;
            });
          this.infos = newData;
          this.infosCount = v.count;
        } else {
          $message("资讯加载失败");
        }
      });
    },
    onTabClick: function(tab, e) {
      if (tab.name === 'news') {
        this.getNewsData();
      } else {
        this.getInfosData();
      }
    },
    onCardClick: function(id) {
      window.open(`/news/newsDetail/${id}`);
    },
    onPageChange: function(i) {
      if (this.activeName === "news") {
        this.getNewsData(i);
      } else {
        this.getInfosData(i);
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>
