<template>
  <div id="container" class="container">
    <img class="banner" :src="bannerImg" alt />
    <div class="tabs">
      <div
        v-for="(v, i) in tabData"
        :key="i"
        :class="
          (tabIndex === i ? 'tabs-select' : 'tabs-tab') +
            (i !== 0 ? ' tab-with-line' : '')
        "
        @click="() => onTabsClick(i, v.type)"
      >
        <div class="tabs-name">{{ v.name }}</div>
      </div>
    </div>
    <div class="line" />
    <div class="main-content">
      <h2 class="container-title">产品展示</h2>
      <div class="content-wrapper">
        <el-card
          :body-style="{ padding: 0 }"
          v-for="(item, index) in prods"
          :key="index"
        >
          <el-image
            :src="item.pic"
            fit="cover"
            :preview-src-list="[item.pic]"
          />
          <div class="card-title">{{ item.title }}</div>
        </el-card>
      </div>
    </div>
    <div class="pagi-wrapper">
      <el-pagination
        class="pagi"
        background
        @current-change="onPageChange"
        :page-size="pageSize"
        :total="count"
      />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { getProds } from "@/utils/service";

export default {
  name: "container",
  components: {},
  data() {
    return {
      prods: undefined,
      count: undefined,
      currentType: undefined,
      pageSize: 6,
      tabIndex: 0,
      tabData: undefined,
      bannerImg: require("@/assets/banner1.jpeg"),
      testImg: "https://s2.ax1x.com/2019/12/15/QWXQW8.png"
    };
  },
  filters: {},
  methods: {
    fetchData: function(pageIndex, pageSize, subType) {
      getProds(pageIndex, pageSize, subType ? subType : null).then(v => {
        if (!v) {
          return;
        }

        const { ok, data, count } = v;
        if (ok) {
          this.prods = data;
          this.count = count;
        }
      });
    },
    onTabsClick: function(index, type) {
      this.tabIndex = index;
      this.currentType = type;
      this.fetchData(1, this.pageSize, type);
    },
    onPageChange: function(i) {
      this.fetchData(i, this.pageSize, this.currentType);
    }
  },
  mounted() {
    this.tabData = this.$store.state.tabData;
    this.fetchData(1, this.pageSize, 0);
  }
};
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>
