import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '@/views/Home/index.vue';
import News from '@/views/News/index.vue';
import NewsDetail from '@/views/News/NewsDetail/index.vue';
import Prod from '@/views/Prod/index.vue';
import Appearance from '@/views/Appearance/index.vue';
import Service from '@/views/Service/index.vue';
import About from '@/views/About/index.vue';
import Contact from '@/views/Contact/index.vue';
import NotFoundPage from '@/views/NotFoundPage.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    // meta: {
    //   title: "连云港泽盈通风管道加工厂",
    //   keywords: "1",
    //   description: "1"
    // },
    component: Home,
  },
  {
    path: '/news',
    name: 'news',
    // meta: {
    //   title: "2",
    //   keywords: "2",
    //   description: "2"
    // },
    component: News,
  },
  {
    path: '/news/newsDetail/:id',
    name: 'newsDetail',
    component: NewsDetail,
    // meta: {
    //   title: "2",
    //   keywords: "2",
    //   description: "2"
    // }
  },
  {
    path: '/prod',
    name: 'prod',
    component: Prod,
    // meta: {
    //   title: "3",
    //   keywords: "3",
    //   description: "3"
    // }
  },
  {
    path: '/appearance',
    name: 'appearance',
    component: Appearance,
    // meta: {
    //   title: "4",
    //   keywords: "4",
    //   description: "4"
    // }
  },
  // {
  //   path: '/service',
  //   name: 'service',
  //   component: Service,
  // },
  {
    path: '/about',
    name: 'about',
    component: About,
  },
  {
    path: '/contact',
    name: 'contact',
    component: Contact,
  },
  {
    path: '/404',
    name: 'notFound',
    component: NotFoundPage,
  },
  {
    path: '*', // 此处需特别注意置于最底部
    redirect: '/404'
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  const kw = document.getElementById('keywords');
  const ds = document.getElementById('description');
  if (kw && to.meta.keywords) {
    kw.setAttribute('content', to.meta.keywords);
  }

  if (ds && to.meta.description) {
    ds.setAttribute('content', to.meta.description);
  }

  if (to.meta.title) {
    document.title = to.meta.title;
  }
  next();
});

export default router;
