import { fetch } from './request';

const getPageData = (sql: string, pageIndex: number = 1, pageSize: number = 10) => (
    fetch(sql, {
        params: {
            pageIndex,
            pageSize,
        },
    }).then((v: any) => v.data)
);

const getPageData2 = (sql: string, pageIndex: number = 1, pageSize: number = 10, subType: any = undefined) => (
    fetch(sql, {
        params: {
            pageIndex,
            pageSize,
            subType,
        },
    }).then((v: any) => v.data)
);

const getDataById = (sql: string, id: any = 0) => (
    fetch(sql, {
        params: {
            id,
        },
    }).then((v: any) => v.data)
);

// /api/getNews
export const getNews = (pageIndex: number = 1, pageSize: number = 10) =>
    (getPageData('/api/getNews', pageIndex, pageSize));

// /api/getInfos
export const getInfos = (pageIndex: number = 1, pageSize: number = 10) =>
    (getPageData('/api/getInfos', pageIndex, pageSize));

// /api/getProds
export const getProds = (pageIndex: number = 1, pageSize: number = 10, subType: any = undefined) =>
    (getPageData2('/api/getProds', pageIndex, pageSize, subType));

// /api/getAppearances
export const getAppearances = (pageIndex: number = 1, pageSize: number = 10) =>
    (getPageData('/api/getAppearances', pageIndex, pageSize));

// /api/getNewsById
export const getNewsById = (id: any) =>
    (getDataById('/api/getNewsById', id));
