<template>
  <div id="container" class="container">
    <img class="banner" :src="bannerImg" alt />
    <div class="content">
      <h2 class="title">{{data && data.title}}</h2>
      <p class="date">{{data && data.date}}</p>
      <el-image v-if="data" :src="data.pic" fit="cover" />
      <p class="info">{{data && data.content}}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { getNewsById } from "@/utils/service";
import NewsCard from "../Components/NewsCard";

export default {
  name: "container",
  components: {
    "news-card": NewsCard
  },
  props: {
    currentType: Number
  },
  data() {
    return {
      bannerImg: require("@/assets/banner1.jpeg"),
      data: undefined,
      pic: undefined,
    };
  },
  filters: {},
  mounted() {
    getNewsById(this.$route.params.id).then(v => {
      if (!v) {
        return;
      }

      const { ok, data } = v;
      if (ok && data && data.length !== 0) {
        this.data = v.data[0];
        this.pic = this.data.pic;
      } else {
        $message("文章加载失败");
      }
    });
  }
};
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>