<template>
  <div id="container" class="container">
    <img class="banner" :src="bannerImg" alt />
    <div class="main">
      <h2 class="main-title">企业风采</h2>
      <div class="appearance-content">
        <waterfall :line-gap="picWidth" :watch="appearances">
          <waterfall-slot
            class="pic-item"
            v-for="(item, index) in appearances"
            :width="100"
            :height="getPicHeight(index)"
            :order="index"
            :key="item.id"
          >
            <el-card class="content-card" :body-style="{}">
              <el-image
                :src="item.pic"
                fit="cover"
                :style="{ width: '100%', height: 'calc(100% - 4em)' }"
                :preview-src-list="[item.pic]"
              />
              <div class="card-title">{{ item.title }}</div>
            </el-card>
          </waterfall-slot>
        </waterfall>
      </div>
      <!-- <div class="pagi-wrapper">
        <el-pagination
          class="pagi"
          background
          :page-size="5"
          :total="count"
          @current-change="onPageChange"
        />
      </div> -->
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Waterfall, WaterfallSlot } from "vue-waterfall";
import { getAppearances } from "@/utils/service";

export default {
  name: "container",
  components: {
    Waterfall,
    WaterfallSlot
  },
  data() {
    return {
      appearances: undefined,
      count: undefined,
      picWidth: (innerWidth * 0.78) / 4, // 0.8
      bannerImg: require("@/assets/banner1.jpeg"),
      testImg: "https://s2.ax1x.com/2019/12/15/QWXQW8.png"
    };
  },
  filters: {},
  mounted() {
    this.fetchData(1, 50);
  },
  methods: {
    fetchData(pageIndex = 1, pageSize = 50) {
      getAppearances(pageIndex, pageSize).then(v => {
        if (!v) {
          return;
        }

        const { ok, data, count } = v;
        if (ok) {
          this.appearances = data;
          this.count = count;
        }
      });
    },
    getPicHeight(index) {
      if (index % 2 === 0) {
        return 100;
      } else if (index % 3 === 0) {
        return 150;
      } else if (index % 5 === 0) {
        return 120;
      }

      return 130;
    },
    onPageChange(i) {
      this.fetchData(i, 8);
    }
  }
};
</script>
