<script>
import axios from "axios";
import { getInfos, getNews, getProds, getAppearances } from "@/utils/service";

export default {
  name: "home",
  components: {},
  data() {
    return {
      news: [],
      infos: undefined,
      prods: undefined,
      appearances: undefined,
      bannerImg: require("@/assets/banner1.jpeg"),
      // loading: true,
      testImg: "https://s2.ax1x.com/2019/12/15/QWXQW8.png"
    };
  },
  filters: {
    currencydecimal(value) {
      return value.toFixed(2);
    }
  },
  methods: {
    onNewsPartClick: function() {},
    onNewsClick: function(id) {
      window.open(`/news/newsDetail/${id}`);
    }
  },
  mounted() {
    getNews(1, 2)
      .then(v => {
        if (!v) {
          return;
        }

        const { ok, data } = v;
        if (ok) {
          const newData = data.map(v => {
            v.date = v.date && v.date.substring(0, 10);
            return v;
          });
          this.news = [...this.news, ...newData];
        }
      })
      .then(() =>
        getInfos(1, 2).then(v => {
          if (!v) {
            return;
          }

          const { ok, data } = v;
          if (ok) {
            const newData = data.map(v => {
              v.date = v.date && v.date.substring(0, 10);
              return v;
            });
            this.news = [...this.news, ...newData];
          }
        })
      );

    getProds(1, 8).then(v => {
      if (!v) {
        return;
      }

      const { ok, data } = v;
      if (ok) {
        this.prods = data;
      }
    });

    getAppearances(1, 8).then(v => {
      if (!v) {
        return;
      }

      const { ok, data } = v;
      if (ok) {
        this.appearances = data;
      }
    });
  }
};
</script>
